import { RouterModule, Routes } from '@angular/router';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
/* Internal */
import { RedactorComponent } from './redactor.component';
import { SharedMoudle } from 'src/app/shared.module';
import { TranslateModule } from '@ngx-translate/core';
import { common_redactor_routes } from 'src/app/common/modules/routes';
import { UsersService } from 'src/app/common/modules/users/users.service';


/**
 * @author Schwada
 * @since 9.1.2018
 *
 * This is a wrapper/template for redactor pages e.g. editor,article editor,
 * dashboard,etc..and should only be accessed by uthenicated users.
 * It also collects all the redactor page routes
 */


export const redactor_routes: Routes = [
	{
    path: '',
    component: RedactorComponent,
		children:[ ...common_redactor_routes ]
	},
]

@NgModule({
  declarations: [ RedactorComponent ],
  imports: [ CommonModule, SharedMoudle, TranslateModule ,RouterModule.forChild(redactor_routes) ],
  providers: [
    [{ provide: UsersService, useClass: UsersService}],
  ],
  exports: [ RouterModule ]
})
export class RedactorModule { }
