import { Component, OnInit } from '@angular/core';
import { AuthenticationService } from '../../services';
import { Router } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { UsersService } from 'src/app/common/modules/users/users.service';


/**
 * @author Schwada
 * @since 11.1.2018
 *
 * The actual component/template that all the
 * redactor pages ( authenticated users ) use
 */


@Component({
	templateUrl: './redactor.template.html',
	styleUrls: ['./redactor.styles.scss'],
})
export class RedactorComponent implements OnInit {


	//move to core dateservice
	days = ['Neděle', 'Pondělí', 'Úterý', 'Středa', 'Čtvrtek', 'Pátek', 'Sobota'];
	months = ["Ledna", "Unora", "Brezna", "Dubna", "Kvetna", "Cervna",
	"Cervence", "Sprena", "Septemberu", "Octobru", "Novemberu", "Decemberu"];

	name;
	day;
	date;

	constructor(
		private authentication: AuthenticationService,
		private userService: UsersService,
		private router: Router
	) {}

	ngOnInit(){
		//get date and notifications and add them to the sidebar
		let now = new Date();


		this.day = this.days[now.getDay()];
		this.date = now.getDate() + "." + this.months[now.getMonth()] + " " + now.getFullYear();


		// get info about user and display on sidebar
		//tohle chceme spravit v authentifikaci aby nam to vrtilo rovnou
		this.userService.getUser(this.authentication._credentials['id']).then(user =>{
			this.name = user.nick;
		});
		//this.userService.getUser();
	}



	logoff(){
		this.authentication.dismissExpiredIdentity().then((data) =>{
			if(data == true){
				 this.router.navigate(['/login']);
			}
		});
	}





}
